import React, { useState, useEffect } from 'react'
import { Report } from 'notiflix/build/notiflix-report-aio';
import Web3Modal from 'web3modal';
import WalletConnect from "@walletconnect/web3-provider";
import { ethers } from 'ethers';
import { getXdcModal } from 'xdcpay-web3modal'
import { useDispatch, useSelector } from "react-redux";
import { setWeb3, clearWeb3 } from '../../../reducers/web3Reducer';




import { AVAILABLE_NETWORK, CHAIN_ID, ModalPopup, NETWORK_NAME, PAYABLE_FARM_CONTRACT, PLUGIN_DEFI_CONTRACT, RECEIVE_FARM_CONTRACT } from '../../../actions/constant';

const web3Modal = new Web3Modal({
    cacheProvider: true,
    disableInjectedProvider: true,
    providerOptions: {
        walletconnect: {
            package: WalletConnect, // required
            options: {
                infuraId: "27e484dcd9e3efcfd25a83a78777cdf1",
                rpc: {
                    50: "https://rpc.xinfin.network",
                    51: "https://erpc.apothem.network"
                },
            }
        },
        'custom-xdc': getXdcModal,
    }
});

const Index = () => {

    const [accountAddress, setaccountAddress] = useState()

    const { connected } = useSelector(state => state.web3);

    const dispatch = useDispatch()


    useEffect(() => {
        if (web3Modal.cachedProvider && !connected) {
            onConnect()
        }
    }, [])

    const onConnect = async () => {


        const instance = await web3Modal.connect();
        const provider = new ethers.providers.Web3Provider(instance);
        const { chainId } = await provider.getNetwork();
        var findNetwork = AVAILABLE_NETWORK.find(x => x.networkID === chainId)

        if (!findNetwork) {
            ModalPopup.warning(
                'Network Mismatch',
                `Please Switch to XDC Apothem`,
                'OK',
            );
            return;
        }
        //CHECK LOGIN OR NOT
        const accounts = await provider.listAccounts();
        if (accounts.length === 0) {
            Report.warning(
                'No Account Connected',
                `Xdc Pay Not LoggedIn.Kindly login your XDC Pay`,
                'OK',
            );
            return;
        }

        //SEND XDC
        const signer = provider.getSigner();
        const address = await signer.getAddress();
        setaccountAddress(address)


        const web3data = {
            provider,
            address,
            signer,
            web3Modal,
            connected: true,
            chainId: chainId,
            symbol: findNetwork.symbol,
            networkName: findNetwork.networkName


        }

        dispatch(setWeb3(web3data))

    }


    return <React.Fragment>

        <div className="mb-0 text-center d-grid">
            {!connected ? <button type="button" onClick={() => onConnect()} className="btn btn-primary"><span>Connect Wallet</span> <em className="icon ni ni-arrow-long-right" /></button>
                : <button type="button" onClick={() => dispatch(clearWeb3())} className="btn btn-primary"><span>Disconnect Wallet</span> <em className="icon ni ni-arrow-long-right" /></button>}
        </div>
    </React.Fragment>

}

export default Index;


